import { supabase } from './supabaseClient'

// Types
export interface ServiceType {
  id: number
  name: string
}

export interface Visitor {
  id: number
  first_name: string
  last_name: string
  phone?: string
  email?: string
  inviter_name?: string
  created_at?: string
}

export interface Service {
  id: number
  user_id: number
  service_type_id: number
  location?: string
  notes?: string
  started_at: string
  ended_at?: string
  created_at?: string
}

export interface Attendance {
  id: number
  service_id: number
  visitor_id: number
  checked_in_at: string
  created_at?: string
}

export interface Member {
  id: number
  first_name: string
  last_name: string
  email?: string
  phone?: string
  location?: string
  department?: string
  status?: string
}

// Supabase Service Class
export class SupabaseService {
  // ==================== VISITORS ====================
  async getVisitors(): Promise<Visitor[]> {
    const { data, error } = await supabase
      .from('visitors')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data || []
  }

  async addVisitor(visitor: {
    first_name: string
    last_name: string
    phone?: string
    email?: string
    inviter_name?: string
  }): Promise<Visitor> {
    const { data, error } = await supabase
      .from('visitors')
      .insert(visitor)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async updateVisitor(id: number, updates: Partial<Visitor>): Promise<Visitor> {
    const { data, error } = await supabase
      .from('visitors')
      .update(updates)
      .eq('id', id)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async deleteVisitor(id: number): Promise<void> {
    const { error } = await supabase.from('visitors').delete().eq('id', id)
    if (error) throw error
  }

  // ==================== SERVICES ====================
  async getServices(): Promise<Service[]> {
    const { data, error } = await supabase
      .from('services')
      .select('*, service_types(name)')
      .order('started_at', { ascending: false })

    if (error) throw error
    return data || []
  }

  async addService(service: {
    user_id?: number
    service_type_id: number
    location?: string
    notes?: string
    started_at: string
    ended_at?: string
  }): Promise<Service> {
    const { data, error } = await supabase
      .from('services')
      .insert({ user_id: 1, ...service })
      .select()
      .single()

    if (error) throw error
    return data
  }

  async updateService(id: number, updates: Partial<Service>): Promise<Service> {
    const { data, error } = await supabase
      .from('services')
      .update(updates)
      .eq('id', id)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async deleteService(id: number): Promise<void> {
    const { error } = await supabase.from('services').delete().eq('id', id)
    if (error) throw error
  }

  // ==================== SERVICE TYPES ====================
  async getServiceTypes(): Promise<ServiceType[]> {
    const { data, error } = await supabase.from('service_types').select('*').order('name')

    if (error) throw error
    return data || []
  }

  async addServiceType(serviceType: { name: string; description?: string; frequency?: string }): Promise<ServiceType> {
    const { data, error } = await supabase
      .from('service_types')
      .insert({ name: serviceType.name })
      .select()
      .single()

    if (error) throw error
    return data
  }

  async updateServiceType(id: number, updates: Partial<ServiceType>): Promise<ServiceType> {
    const { data, error } = await supabase
      .from('service_types')
      .update(updates)
      .eq('id', id)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async deleteServiceType(id: number): Promise<void> {
    const { error } = await supabase.from('service_types').delete().eq('id', id)
    if (error) throw error
  }

  // ==================== ATTENDANCE ====================
  async getAttendance(): Promise<any[]> {
    const { data, error } = await supabase
      .from('attendance')
      .select(
        `
        *,
        visitors(first_name, last_name),
        services(service_types(name))
      `
      )
      .order('checked_in_at', { ascending: false })

    if (error) throw error
    return data || []
  }

  async addAttendance(attendance: {
    service_id: number
    visitor_id: number
    checked_in_at: string
  }): Promise<Attendance> {
    const { data, error } = await supabase
      .from('attendance')
      .insert(attendance)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async updateAttendance(id: number, updates: Partial<Attendance>): Promise<Attendance> {
    const { data, error } = await supabase
      .from('attendance')
      .update(updates)
      .eq('id', id)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async deleteAttendance(id: number): Promise<void> {
    const { error } = await supabase.from('attendance').delete().eq('id', id)
    if (error) throw error
  }

  // ==================== MEMBERS ====================
  async getMembers(): Promise<Member[]> {
    const { data, error } = await supabase
      .from('members')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data || []
  }

  async addMember(member: {
    first_name: string
    last_name: string
    email?: string
    phone?: string
    location?: string
    department?: string
    status?: string
  }): Promise<Member> {
    const { data, error } = await supabase
      .from('members')
      .insert({
        first_name: member.first_name,
        last_name: member.last_name,
        email: member.email || null,
        phone: member.phone || null,
        location: member.location || null,
        department: member.department || null,
        status: member.status || 'Active',
      })
      .select()
      .single()

    if (error) throw error
    return data
  }

  async updateMember(id: number, updates: Partial<Member>): Promise<Member> {
    const { data, error } = await supabase
      .from('members')
      .update(updates)
      .eq('id', id)
      .select()
      .single()

    if (error) throw error
    return data
  }

  async deleteMember(id: number): Promise<void> {
    const { error } = await supabase.from('members').delete().eq('id', id)
    if (error) throw error
  }
}

export const supabaseService = new SupabaseService()

